<template>
    <div class="warp">
        <img class="user_img" :src="require('../../assets/xy.png')" alt="网站Logo">
        <div class="form">
            <form class="no_login">
                <label class="login_phone">
                    <span class="iconfont">&#xe692;</span>
                    <input v-model="phone" type="text"  maxlength="11" oninput="value=value.replace(/[^0-9.]/g,'')" placeholder="请输入手机号">
                </label>
                <label class="login_passWord">
                    <span class="iconfont">&#xe623;</span>
                    <input v-model="passWord" type="password" placeholder="请输入密码">
                </label>
                <button class="login_show" @click.prevent="judegLogin">登录</button>
                <router-link class="to_register" to="/register">没有账号，前往注册?</router-link>
            </form>
        </div>
    </div>
</template>

<style lang="scss" scoped>
    .warp{
        width: 100%;
        height: 100%;
        .user_img{
            width: 6.25rem;
            height: 6.25rem;
            border-radius: 6.25rem;
            margin-left: 50%;
            margin-top: 20%;
            transform: translateX(-50%);
        }
        .form{
            margin: 0 auto;
            width: 100%;
            height: 26.666667rem;
            // margin-top: 5.333333rem;
            margin-top: 1.875rem;
            .no_login{
                width: 90%;
                margin: 0 auto;
                label{
                    display: block;
                    width: 100%;
                    height: 2.666667rem;
                    margin-top: 1.25rem;
                    margin-bottom: .9375rem;
                    line-height: 2.666667rem;
                    border: 1px solid #ccc;
                    border-radius: .266667rem;
                    padding: .266667rem;
                    input{
                        width: 90%;
                        height: 100%;
                    }
                    span{
                        vertical-align: middle;
                        margin-right: .266667rem;
                        font-size: 1.25rem;
                    }
                }
                button{
                    width: 100%;
                    height: 3.2rem;
                    margin-top: 1.066667rem;
                    background-color: brown;
                    color: #fff;
                    border-radius: .4rem;
                    font-size: 1.066667rem;
                }
                .to_register{
                    display: inline-block;
                    width: 12.5rem;
                    text-align: center;
                    margin-left: 50%;
                    transform: translateX(-50%);
                    margin-top: 1.25rem;
                }
            }
        }
    }
</style>

<script>
import { Toast } from 'vant'
import { post } from '../../untils/request'
export default {
    name: 'Content',
    data () {
        return {
            phone: '',
            passWord: ''
        }
    },
    methods: {
        judegLogin: async function () {
            const result = await post('/register', {
                phone: this.phone,
                password: this.passWord
            })
            if (result.code === 200) {
                for (const item in this.$store.state.user) {
                    if (this.phone === this.$store.state.user[item].phone && this.passWord === this.$store.state.user[item].password) {
                        localStorage.isLogin = true
                        this.$router.push('/')
                        this.$store.commit('setBottomIndex', { index: 0, commodityItem: '精选' })
                        Toast('登录成功')
                        break
                    } else {
                        Toast('信息错误')
                    }
                }
            }
        }
    }
}
</script>
